  z-index: -1; }

.cybr-btn:before {
  background: var(--shadow-primary);
  transform: translate(var(--border), 0); }

.cybr-btn:after {
  background: var(--primary); }

.cybr-btn__tag {
  position: absolute;
  padding: 1px 4px;
  letter-spacing: 1px;
  line-height: 1;
  bottom: -5%;
