@import '../../static/styles/base/base';
.backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.75);
  display: flex;
  align-items: flex-start;
  justify-content: center;
  z-index: 100;
  padding-top: 70px;
  overflow-y: auto;
}

.modal-container {
  background-color: #fff;
  max-width: 560px;
  min-height: 300px;
  max-height: calc(100vh - 140px);
  margin: 0 auto;
  padding: 0px;
  overflow-y: auto;
  border-radius: 8px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  .modal-header {
    border-top: 6px solid $dm-highlight;
    border-bottom: 1px solid rgb(227, 227, 227);
    min-height: 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    padding: 0px 20px;
    position: relative;
  }
  .modal-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .modal-description-heading {
    font-size: 16px;
  }
  .modal-description {
    font-size: 14px;
  }
  .modal-title {
    color: #111;
    font-family: Montserrat, sans-serif;
    font-size: 18px;
  }
  .modal-close {
    color: $dm-highlight;
    font-size: 30px;
    margin-top: -5px;
    &:hover {
      cursor: pointer;
      color: #111;
    }
  }
  .modal-headline {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid rgba(227, 227, 227, 0.5);
    padding-bottom: 10px;
    margin-bottom: 10px;
    .modal-headine-percentage {
      display: flex;
      flex-direction: row;
    }
    .tech-label {
      margin-left: 0px;
      padding-left: 5px;
    }
  }
  .modal-time,
  .modal-authors {
    font-family: $font-science;
    color: $dm-highlight;
  }
}

.volunteer-modal {
  height: auto;
}

body.dark-mode {
  .modal-container,
  .modal-header,
  .modal-title {
    background-color: #25282a !important;
    color: rgb(227, 227, 227) !important;
  }
}

.close-btn {
  border: none;
  background-color: transparent;

  &:hover {
    cursor: pointer;
  }
}

// Mobile responsive adjustments
@media (max-width: 768px) {
  .backdrop {
    padding-top: 20px;
    padding-left: 10px;
    padding-right: 10px;
  }
  
  .modal-container {
    max-width: 100%;
    max-height: calc(100vh - 40px);
    margin: 0;
  }
}